* {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
  box-sizing: border-box;
}


body {
  background-color: #45aaf2;
  transition: 0.6s linear;
}

.product-card {
  width: 400px;
  background: #ecedef;
  padding: 40px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  text-transform: uppercase;

h2 {
    font-size: 22px;
    margin-bottom: 4px;
  }

p {
    font-size: 13px;
    color: #bbb;
  }
}

.product-pic {
  background-image: url("../img/1.png");
  width: 100%;
  height: 200px;
  background-size: cover;
  background-position: center;

}

.product-colors {
  margin-bottom: 40px;
  display: flex;
  justify-content: center;

  span {
    width: 14px;
    height: 14px;
    margin: 0 10px;
    border-radius: 50%;
    cursor: pointer;
    position: relative;
  }
}
.blue{
  background-color: #45aaf2;
}

.Green {
  background-color: #26de81;
}
.Yellow {
  background-color: #fed330;

}
.Rose {
  background-color: #fc5c65;
}

.product-colors .active:after {
  content: "";
  width: 22px;
  height: 22px;
  border: 2px solid #8888;
  position: absolute;
  border-radius: 50%;
  box-sizing: border-box;
  left: -4px;
  top: -4px;
}

.product-info {
  display: flex;
  align-items: center;
}

.product-price {
  color: #45aaf2;
  font-size: 26px;
}

.product-button {
  margin-left: auto;
  color: #45aaf2;
  text-decoration: none;
  border: 2px solid;
  padding: 8px 24px;
  border-radius: 20px;
  transition: .4s linear;
}

.product-button:hover {
  transform: scale(1.06);
}
